const burger = () => {
    const menu = document.querySelector('.nav-burger__menu')
    const button = document.querySelector('.nav-burger__btn')
    const overlay = document.querySelector('.nav-burger__overlay')
    const lines = document.querySelector('.nav-burger__lines')
    const links = document.querySelectorAll('.nav-burger__menu li')

    button.addEventListener('click', (e) => {
        e.preventDefault()
        toggleMenu()
    })

    links.forEach(item => {
        item.addEventListener('click', toggleMenu)
    })

    overlay.addEventListener('click', toggleMenu)

    function toggleMenu() {
        menu.classList.toggle('nav-burger__menu--active')
        lines.classList.toggle('nav-burger__lines--active')
        overlay.classList.toggle('nav-burger__overlay--active')
        
        if (menu.classList.contains('nav-burger__menu--active')) {
            document.querySelector('body').style.overflow = 'hidden'
        } else {
            document.querySelector('body').style.overflow = 'visible'
        }
    }
}

export default burger
