import formattingPrice from './utils'

const cart = () => {
    readStorage()
    renderCartItems()

    const btnInCart = document.querySelectorAll('.btn-incart-js')
    const btnRemove = document.querySelectorAll('.good-delete')
    const btnInCartCard = document.querySelector('#addToCartBtn')
    let arrayDoors = []
    let arrayPanels = []
    let arrayServices = []

    const renderCartEmpty = () => {
        readStorage()
        const cart = document.querySelector('.cart')
        const markup = '<p class="cart-empty">Ваша корзина пуста</p>'
        
        if (cart && arrayDoors.length <= 0) {
            cart.innerHTML = markup
            localStorage.clear()
        } 
    }
    renderCartEmpty()

    const modalThank = (modalSelector, closeSelector) => {
        const modal = document.querySelector(modalSelector)
        const close = document.querySelector(closeSelector)

        modal.classList.add('fadein')
        modal.style.display = 'block'
                    
        const none = () => { modal.style.display = 'none' }

        close.addEventListener('click', () => {
            modal.classList.remove('fadein')
            modal.classList.add('fadeout')
            setTimeout(none, 300)
        })
        
        
        modal.addEventListener('click', (e) => {
            if (e.target === modal) {
                modal.classList.remove('fadein')
                modal.classList.add('fadeout')
                setTimeout(none, 300)
            }
        })
    }

    btnInCart.forEach(item => {
        item.addEventListener('click', (e) => {
            readStorage()
            const price = parseInt(item.previousElementSibling.firstElementChild
                .textContent.replace(/[^\d]/g, ''))
            const name = item.previousElementSibling.previousElementSibling.previousElementSibling
                .firstElementChild.textContent
            
            const cartCurrentItem = {
                name: name,
                price: price,
                count: 1
            }
            
            if (arrayDoors.length) {
                if (arrayCheck(arrayDoors, name)) {
                    arrayDoors.forEach(item => {
                        if (cartCurrentItem.name === item.name) {
                            item.count++
                        }
                    })
                } else {
                    arrayDoors.push(cartCurrentItem)
                }
                
            } else {
                arrayDoors.push(cartCurrentItem)
            }
            
            saveData(arrayDoors, 'doors')
            cartCount()
            modalThank('.popup-overlay__4', '.js-btn-back1')
        })
    })

    if (btnInCartCard) {
        btnInCartCard.addEventListener('click', (e) => {
            readStorage()
            const name = document.querySelector('.card-door-spec__title h2 p').textContent
            const price = parseInt(document.querySelector('.card-door-spec__price-price')
                .dataset.price.replace(/[^\d]/g, ''))
            const panel = document.querySelector('.color-select__item--active')
            const selectedCheckBoxes = document.querySelectorAll('input.tab-services__real-checkbox:checked')

            const cartCurrentItem = {
                name: name,
                price: price,
                count: 1
            }

            if (arrayDoors.length) {
                if (arrayCheck(arrayDoors, name)) {
                    arrayDoors.forEach(item => {
                        if (cartCurrentItem.name === item.name) {
                            item.count++
                        }
                    })
                } else {
                    arrayDoors.push(cartCurrentItem)
                }
                
            } else {
                arrayDoors.push(cartCurrentItem)
            }
            

            if (panel) { 
                const panelObj = { 
                    name: panel.dataset.name, 
                    price: parseInt(panel.dataset.panel.replace(/[^\d]/g, '')),
                    count: 1
                }

                if (arrayPanels.length) {
                    if (arrayCheck(arrayPanels, panel.dataset.name)) {
                        arrayPanels.forEach(item => {
                            if (panelObj.name === item.name) {
                                item.count++
                            }
                        })
                    } else {
                        arrayPanels.push(panelObj)
                    }
                    
                } else {
                    arrayPanels.push(panelObj)
                }
            }
            
            if (selectedCheckBoxes.length) {
                selectedCheckBoxes.forEach(item => {
                    const name = item.nextElementSibling.nextElementSibling.firstElementChild.textContent
                    const price = parseInt(item.nextElementSibling.nextElementSibling.lastElementChild
                        .dataset.price.replace(/[^\d]/g, ''))
                    
                    let count = 1
                    
                    if (item.nextElementSibling.nextElementSibling.nextElementSibling) {
                        count = parseInt(item.nextElementSibling.nextElementSibling.nextElementSibling
                            .firstElementChild.nextElementSibling.textContent.replace(/[^\d]/g, ''))
                    }
                    
                    
                    const servicesObj = {
                        name: name,
                        price: price,
                        count: count
                    }
                    
                    if (arrayServices.length) {
                        if (arrayCheck(arrayServices, servicesObj.name)) {
                            arrayServices.forEach(item => {
                                if (servicesObj.name === item.name) {
                                    item.count++
                                }
                            })
                        } else {
                            arrayServices.push(servicesObj)
                        }
                        
                    } else {
                        arrayServices.push(servicesObj)
                    }
                })
            }

            saveData(arrayDoors, 'doors')
            saveData(arrayPanels, 'panels')
            saveData(arrayServices, 'services')
            cartCount()
            modalThank('.popup-overlay__4', '.js-btn-back1')
        })
    }
    
    btnRemove.forEach(item => {
        item.addEventListener('click', (e) => {
            readStorage()
            const name = item.previousElementSibling.previousElementSibling.previousElementSibling
                .firstElementChild.textContent
            const type = item.previousElementSibling.previousElementSibling.previousElementSibling
                .firstChild.textContent.replace(/\s/g, '').toLowerCase()
            
            if (type === 'стальнаядверь') {
                removeItem(arrayDoors, 'doors', name)
            } else if (type === 'внутренняяпанель') {
                removeItem(arrayPanels, 'panels', name)
            } else if (type === '') {
                removeItem(arrayServices, 'services', name)
            }

            e.target.parentNode.parentNode.remove()
            calcTotal()
            formattingPrice()
        })
    })

    function removeItem(arr, nameLS, name) {
        const index = arr.findIndex(n => n.name === name)
        arr.splice(index, 1)
        saveData(arr, nameLS)
    }

    function arrayCheck(arr, name) {
        const found = arr.some(function(elem) {
            return elem.name === name
        })
        return found
    }

    function saveData(obj, nameLS) {
        localStorage.setItem(nameLS, JSON.stringify(obj))
    }

    function readStorage() {
        const storageDoors = JSON.parse(localStorage.getItem('doors'))
        const storagePanels = JSON.parse(localStorage.getItem('panels'))
        const storageServices = JSON.parse(localStorage.getItem('services'))

        if (storageDoors) { arrayDoors = storageDoors }
        if (storagePanels) { arrayPanels = storagePanels }
        if (storageServices) { arrayServices = storageServices }
    }

    function renderCartItems() {
        readStorage()
        const goodsContainer = document.querySelector('.goods-container')
        const goodsContainerServices = document.querySelector('.goods-container-services')
        
        if (goodsContainer || goodsContainerServices) {
            if (arrayDoors) { renderItem(arrayDoors, goodsContainer, 'Стальная дверь') }
            if (arrayPanels) { renderItem(arrayPanels, goodsContainer, 'Внутренняя панель') }
            if (arrayServices) { renderItem(arrayServices, goodsContainerServices, '') }
        }
    }

    function renderItem(arr, container, prefix) {
        if (arr.length) {
            arr.forEach(item => {
                const markup = `<div class="cart-row">
                    <div class="good-name">${prefix} <span>${item.name}</span></div>
                    <div class="good-amount"><span>${item.count}</span> шт.</div>
                    <div class="good-price price">${item.price * item.count}</div>
                    <div class="good-delete">
                        <img src="/img/cart.svg" alt="delete">
                        <span>Удалить</span>
                    </div>
                </div>`

                container.insertAdjacentHTML('beforeend', markup)
            })
        }
    }

    function calcTotal() {
        const totalPrice = document.querySelector('.cart-total span')
        const prices = document.querySelectorAll('.good-price')
        let sum = 0
        
        if (totalPrice) {
            prices.forEach(item => {
                const currentPrice = parseInt(item.textContent.replace(/[^\d]/g, ''))
                sum = sum + currentPrice
            })
            totalPrice.textContent = sum
        }
    }

    function cartCount() {
        readStorage()
        let sum = 0
        const cartIcon = document.querySelector('.nav-cart')
        
        if (arrayDoors.length || arrayPanels.length || arrayServices.length) {
            cartIcon.classList.add('nav-cart--notempty')
            sum = sumCount(arrayDoors) + sumCount(arrayPanels) + sumCount(arrayServices)
            cartIcon.lastElementChild.textContent = sum
        }
    }

    function sumCount(arr) {
        let sum = 0
        arr.forEach(item => {
            sum = sum + item.count
        })
        return sum
    }
    
    // Отправка заказа
    const sendData = async (url, data) => {
        const response = await fetch(url, {
            method: 'POST',
            body: data
        })

        if (!response.ok) {
            throw new Error(`Ошибка по адресу ${url}, статус ошибки ${response.status}`)
        }

        return await response.text()
    }

    const validateInput = (formField) => {
        formField.classList.add('form-error')
        formField.previousElementSibling.classList.add('form-error-text')

        formField.addEventListener('focus', () => {
            formField.classList.remove('form-error')
            formField.previousElementSibling.classList.remove('form-error-text')
        })
    }

    const sendCart = () => {
        readStorage()
        const cartForm = document.querySelector('.cart-form')

        const data = {
            doors: arrayDoors,
            panels: arrayPanels,
            services: arrayServices
        }
        
        if (cartForm) {
            cartForm.addEventListener('submit', (e) => {
                e.preventDefault()

                if (arrayDoors.length <= 0) {
                    alert('Корзина пуста!')
                    return false
                }

                if (cartForm.name.value == '') {
                    validateInput(cartForm.name)
                    return false
                } else if (cartForm.phone.value == '') {
                    validateInput(cartForm.phone)
                    return false
                } else if (cartForm.address.value == '') {
                    validateInput(cartForm.address)
                    return false
                } else if (cartForm.check.checked) {
                    const cart = document.querySelector('.cart')
                    const formData = new FormData(cartForm)
                    formData.set('orderDoors', JSON.stringify(data.doors))
                    formData.set('orderPanels', JSON.stringify(data.panels))
                    formData.set('orderServices', JSON.stringify(data.services))

                    sendData('/cart', formData)
                        .then(() => {
                            cartForm.reset()
                            modalThank('.popup-overlay__5', '.js-btn-back')
                            localStorage.clear()
                            cart.innerHTML = '<p class="cart-empty">Ваша корзина пуста</p>'
                        })
                        .catch((err) => {
                            console.log(err)
                        })
                } else {
                    cartForm.check.nextElementSibling.classList.add('form-error')
                    cartForm.check.parentNode.addEventListener('click', () => {
                        if (cartForm.check.checked) {
                            cartForm.check.nextElementSibling.classList.remove('form-error')
                        }
                    })
                    return false
                }
            })
        }
    }
    
    sendCart()
    calcTotal()
    cartCount()
    formattingPrice()
}

export default cart