import tabs from  './modules/tabs'
import modals from './modules/modals'
import forms from './modules/forms'
import images from './modules/images'
import calc from './modules/calc'
import burger from './modules/burger'
import badges from './modules/badges'
import cart from './modules/cart'
import './modules/sliders'
import './modules/utils'

window.addEventListener('DOMContentLoaded', () => {
    modals('.back-call-btn', '.popup-overlay__1', '.popup-overlay__1 .js-close')
    modals('.btn-call_1', '.popup-overlay__2', '.popup-overlay__2 .js-close')
    modals('.btn-call_2', '.popup-overlay__1', '.popup-overlay__1 .js-close')
    modals('.card-door-spec__btn-buynow', '.popup-overlay__3', '.popup-overlay__3 .js-close')

    tabs('.card-door__tabs', '.card-door__tab', '.tab-content', 'card-door__tab--active')

    if (document.querySelector('.card-door-spec__panel')) {
        tabs('.color-select', '.color-select__item', '.img-panel-wrapper', 'color-select__item--active')
    }
    
    forms()
    images()
    calc()
    burger()
    badges()
    cart()
})
