const forms = () => {
    const form = document.querySelectorAll('.js-form')
    const inputs = document.querySelectorAll('input')
    const phoneInputs = document.querySelectorAll('input[name="phone"]')

    phoneInputs.forEach(item => {
        item.addEventListener('input', () => {
            item.value = item.value.replace(/\D/, '')
        })
    })

    const closePopup = (form) => {
        const modal = form.parentNode.parentNode
        const modalThank = document.querySelector('.popup-overlay__6')
        const modalThankClose = document.querySelector('.btn-ok')

        const none = () => { modal.style.display = 'none' }
        const noneThank = () => { modalThank.style.display = 'none' }

        const popupThank = () => {
            modalThank.classList.add('fadein')
            modalThank.classList.remove('fadeout')
            modalThank.style.display = 'block'
            document.body.classList.add('hidden')

            modalThankClose.addEventListener('click', () => {
                modalThank.classList.remove('fadein')
                modalThank.classList.add('fadeout')
                setTimeout(noneThank, 300)
                document.body.classList.remove('hidden')
            })

            modalThank.addEventListener('click', (e) => {
                if (e.target === modalThank) {
                    modalThank.classList.remove('fadein')
                    modalThank.classList.add('fadeout')
                    setTimeout(noneThank, 300)
                    document.body.classList.remove('hidden')
                }
            })
        }

        if (form.classList.contains('popup-form')) {
            modal.classList.remove('fadein')
            modal.classList.add('fadeout')
            setTimeout(none, 300)
            document.body.classList.remove('hidden')
            popupThank()
        } else {
            popupThank()
        }
    }

    const postData = async (url, data) => {
        let result = await fetch(url, {
            method: 'POST',
            body: data
        })
        
        return await result.text()
    }

    const clearInputs = () => {
        inputs.forEach(item => {
            item.value = ''
        })
    }

    form.forEach(item => {
        item.addEventListener('submit', (e) => {
            e.preventDefault()

            if (item.name.value == '') {
                item.name.classList.add('form-error')
                item.name.previousElementSibling.classList.add('form-error-text')
                
                item.name.addEventListener('focus', () => {
                    item.name.classList.remove('form-error')
                    item.name.previousElementSibling.classList.remove('form-error-text')
                })
                
                return false
            } else if (item.phone.value == '') {
                item.phone.classList.add('form-error')
                item.phone.previousElementSibling.classList.add('form-error-text')
                
                item.phone.addEventListener('focus', () => {
                    item.phone.classList.remove('form-error')
                    item.phone.previousElementSibling.classList.remove('form-error-text')
                })
                
                return false
            } else if (item.check.checked) {
                const formData = new FormData(item)
            
                postData('/backcall', formData)
                    .then(result => {
                        closePopup(item)
                    })
                    .catch((err) => {
                        console.log(err)
                    })
                    .finally(() => {
                        clearInputs()
                    })
            } else {
                item.check.nextElementSibling.classList.add('form-error')
                item.check.parentNode.addEventListener('click', () => {
                    if (item.check.checked) {
                        item.check.nextElementSibling.classList.remove('form-error')
                    }
                })
                return false
            }
        })
    })
}

export default forms