const gallerySlider = $('#gallerySlider')
const colorSelectSlider = $('#colorSelectSlider')

gallerySlider.owlCarousel({
    items: 1,
    nav: true,
    navText: [
        '<div class="gallery-slider__control-left"><img src="/img/slider-left.svg" alt="left"></div>',
        '<div class="gallery-slider__control-right"><img src="/img/slider-right.svg" alt="right"></div>'
    ],
    dots: false,
    loop: true,
    responsive: {
        400:{
            items: 2
        },
        600:{
            items: 3
        }
    }
})

colorSelectSlider.owlCarousel({
    items: 5,
    nav: true,
    navText: [
        '<div class="select-color__control-left"><img src="/img/card/left_card.svg" alt="left"></div>',
        '<div class="select-color__control-right"><img src="/img/card/right_card.svg" alt="right"></div>'
    ],
    dots: false
})
