const images = () => {
    const imgPopup = document.createElement('div')
    const workSection = document.querySelector('.catalog__wrapper')
    const bigImage = document.createElement('img')
    
    if (workSection) {
        imgPopup.classList.add('popup-overlay__1')
        workSection.appendChild(imgPopup)

        imgPopup.style.justifyContent = 'center'
        imgPopup.style.alignItems = 'center'
        bigImage.classList.add('door-bigImg')
        imgPopup.appendChild(bigImage)

        workSection.addEventListener('click', (e) => {
            e.preventDefault()

            if (e.target && e.target.classList.contains('preview')) {
                imgPopup.style.display = 'flex'
                const path = e.target.getAttribute('src')
                bigImage.setAttribute('src', path)
            }

            if (e.target && e.target.matches('div.popup-overlay__1')) {
                imgPopup.style.display = 'none'
            }
        })
    }
}

export default images
