// Стрелка 'UP'
$('.backTop').hide()
$(window).scroll(function(){
    if($(this).scrollTop() > 200){
        $('.backTop').fadeIn()
    } 
    else{
        $('.backTop').fadeOut()
    }
})

// костыль для бейджа в каталоге
function badgeTop(badgeSelector, ptSelector) {
    const badge = document.querySelectorAll(badgeSelector)
    badge.forEach(item => {
        if (item.textContent == '') {
            item.parentElement.classList.add(ptSelector)
        }
    })
}

badgeTop('.catalog-page__card-badge', 'catalog-page__card-plate-pt33')
badgeTop('.catalog__card-badge', 'catalog__card-plate-pt31')

// Форматирование цен
const formattingPrice = (price = false) => {
    if (!price) {
        const price = document.querySelectorAll('.price')

        price.forEach(item => {
            if (item.textContent !== '') {
                let newItem = parseInt(item.textContent.replace(/[^\d]/g, ''))
                let resultItem = new Intl.NumberFormat('ru-RU').format(newItem)
                item.textContent = resultItem + ' \u20BD'
            }
        })
    } else {
        let newPrice = new Intl.NumberFormat('ru-RU').format(price)
        newPrice = newPrice + ' \u20BD'
        return newPrice
    }
}

formattingPrice()


// Двери без выбора панелей
const doorName = document.querySelectorAll('.card-door-spec__title h2 p')
const img = document.querySelector('.card-door__img-panel')
const olimpia = `
    <div class="img-panel-wrapper faded"> 
        <img src="/img/card/panel/olimpia.jpg" alt="olimpia"> 
    </div>`
const interma =  `
    <div class="img-panel-wrapper faded"> 
        <img src="/img/card/panel/interma-guard.jpg" alt="interma-guard"> 
    </div>`

function panelNot(string, objHTML) {
    if (doorName[0].textContent.replace(/\s/g, '').toLowerCase() == string.toLowerCase()) {
        img.insertAdjacentHTML('afterbegin', objHTML)
    }
}

if (doorName.length) {
    panelNot('Олимпия', olimpia)
    panelNot('ИНТЕРМА-СТРАЖ', interma)
}

// Цвет текста в зависимости от панели
const changeTextColor = () => {
    const panelName = document.querySelectorAll('.img-panel__name span')
    const arrGray = [
        'Сан Ремо 1 RAL 9003', 'Сан Ремо 1 RAL 9010 (цвет на фото может не совпадать)', 
        'Валентия 2 - Ясень жемчуг ШПОН', 'ФЛ-243-м белая матовая', 'ФЛ-316 белый ясень', 
        'Дуб сильвер поперечный', 'Лучи милк', 'ФЛ-39 Сандал белый', 'L-5 Сандал белый'
    ]

    if (panelName) {
        panelName.forEach(item => {
            if (arrGray.indexOf(item.textContent) != -1) {
                item.parentNode.classList.add('name-color__gray')
            } else if (item.textContent == 'Белая матовая с зеркалом ФЛЗ-120') {
                item.parentNode.classList.add('mw-mirror')
            } else if (item.textContent == 'Позитано - Дуб бургундский ШПОН') {
                item.parentNode.classList.add('mw140')
            } else if (item.textContent == 'Фоджа 7 - Дуб бургундский ШПОН') {
                item.parentNode.classList.add('fodga7', 'mw140')
            } else if (item.textContent == 'Фоджа 7 - Ясень жемчуг ШПОН') {
                item.parentNode.classList.add('fodga7', 'mw115', 'name-color__gray')
            }
        })
    }
}

changeTextColor()


// Вывод определенного кол-ва карточек на главной
const cardsNone = (cards, a, b) => {
    if (cards.length > a) {
        cards.forEach((item, index) => {
            if (index > b) {
                item.style.display = 'none'
            }
        })
    }
}

const cardsMainPage = () => {
    const cards = document.querySelectorAll('.catalog__card')

    if (window.innerWidth >= 1200) {
        cardsNone(cards, 8, 7)
    } else if (window.innerWidth >= 768 && window.innerWidth <= 1200) {
        cardsNone(cards, 6, 5)
    } else if (window.innerWidth < 768) {
        cardsNone(cards, 4, 3)
    }
}

cardsMainPage()

// Блок каталог под шапкой на мобиле
const mobileCatalog = () => {
    const main = document.querySelector('main')
    const catalog = document.querySelector('.catalog')
    const mainBlock = document.querySelector('.main')
    const section = document.querySelectorAll('section')
    
    if (catalog) {
        if (window.innerWidth < 768) {
            main.classList.add('modile-catalog-main')
            // catalog.classList.add('order-2')
            // mainBlock.classList.add('order-1')
    
            section.forEach((item, i) => {
                item.style.order = `${i}`
            })
    
            catalog.style.order = 0
        }
    }
}

mobileCatalog()

export default formattingPrice