// Табы карточки описание
const tabs = (headerSelector, tabSelector, contentSelector, activeClass) => {
    const header = document.querySelector(headerSelector)
    const tab = document.querySelectorAll(tabSelector)
    const content = document.querySelectorAll(contentSelector)
    const doorName = document.querySelectorAll('.card-door-spec__title h2 p')

    function hideTabContent() {
        content.forEach(item => {
            item.style.display = 'none'
        })

        tab.forEach(item => {
            item.classList.remove(activeClass)
        })
    }

    function showTabContent(i) {
        
        if (activeClass == 'color-select__item--active') {
            
            if (i >= 0) {
                document.querySelector('.js-badge').classList.remove('pt-badge')
                content[i].style.display = 'block'
                tab[i].classList.add(activeClass)
            } else {
                document.querySelector('.js-badge').classList.add('pt-badge')
            }
            
        } else {
            if (!i) { i = 0 }
            content[i].style.display = 'block'
            tab[i].classList.add(activeClass)
        }
        // if (!i) { i = 0 }
        // content[i].style.display = 'block'
        // tab[i].classList.add(activeClass)

        if (doorName[0].textContent.replace(/\s/g, '').toLowerCase() == 'Олимпия'.toLowerCase() || 
            doorName[0].textContent.replace(/\s/g, '').toLowerCase() == 'ИНТЕРМА-СТРАЖ'.toLowerCase()) {
            
            document.querySelector('.js-badge').classList.remove('pt-badge')
            if (!i) { i = 0 }
            content[i].style.display = 'block'
            tab[i].classList.add(activeClass)
        } 
       
    }
    
    if (content.length !== 0) {
        hideTabContent()
        showTabContent()
    }
    
    if (header) {
        header.addEventListener('click', (e) => {
            if (e.target && e.target.classList.contains(tabSelector.replace(/\./, ""))) {
                tab.forEach((item, i) => {
                    if (e.target == item) {
                        hideTabContent()
                        showTabContent(i)
                    }
                })
            }
        })
    }
}

export default tabs