const badges = () => {
    const badgesMain = document.querySelectorAll('.catalog__card-badge')
    const badgesCatalog = document.querySelectorAll('.catalog-page__card-badge')
    const badgesCard = document.querySelectorAll('.card-door-spec__badge')

    function toggleClass(badges, string, addClass) {
        badges.forEach(item => {
            if (item.textContent.replace(/\s/g, '') == string) {
                item.classList.add(addClass)
            }
        })
    }

    toggleClass(badgesMain, 'Распродажа', 'badge-sale')
    toggleClass(badgesCatalog, 'Распродажа', 'badge-sale')
    toggleClass(badgesCard, 'Нетвналичии', 'card-door-spec__badge--not')
}

export default badges