import formattingPrice from './utils'

const calc = () => {
    const servicePrices = document.querySelectorAll('.tab-services__item-price')
    const servicesItems = document.querySelectorAll('.tab-services__real-checkbox')
    const panelItem = document.querySelectorAll('.color-select__item')
    const countDoors = document.querySelector('.count')
    const minus = document.querySelector('.btn-count__minus')
    const plus = document.querySelector('.btn-count__plus')
    const totalPrice = document.querySelector('.card-door-spec__price-price')
    
    const countServicesGroup = document.querySelectorAll('.services-btn-count-group')

    function showPrice(currentPrice) {
        totalPrice.textContent = formattingPrice(currentPrice)
    }

    if (servicePrices.length > 0) {
        const priceOneDoor = parseInt(totalPrice.textContent.replace(/[^\d]/g, ''))
        let currentTotalPrice = parseInt(totalPrice.textContent.replace(/[^\d]/g, ''))
        
        panelItem.forEach((item, i) => {
            const pricePanel = parseInt(item.getAttribute('data-panel'))
            
            // if (i == 0) {
            //     currentTotalPrice = priceOneDoor + pricePanel
            //     showPrice(currentTotalPrice)
            // }

            item.addEventListener('click', () => {
                const string = document.querySelector('.card-door-spec__price h5 span')
                const oldPrice = document.querySelector('.card-door-spec__old-price-price')

                string.style.display = 'block'
                oldPrice.style.display = 'none'

                currentTotalPrice = priceOneDoor + pricePanel
                servicesItems.forEach(item => { item.checked = false })
                // countDoors.textContent = 1
                countServicesGroup.forEach(item => { 
                    let count = item.querySelector('.services-count').textContent
                    let oneServicePrice = item.previousElementSibling.lastElementChild
                        .textContent.replace(/[^\d]/g, '')
                    item.previousElementSibling.lastElementChild.textContent = 
                        formattingPrice(oneServicePrice / count)
                    item.querySelector('.services-count').textContent = 1
                })
                showPrice(currentTotalPrice)
            })
        })

        

        servicesItems.forEach(services => {
            let servicePriceTotal = parseInt(services.parentNode.querySelector('.tab-services__item-price')
                .textContent.replace(/[^\d]/g, ''))
            
            countServicesGroup.forEach(item => {
                const countServices = item.querySelector('.services-count')
                const minusServices = item.querySelector('.services-btn-count__minus')
                const plusServices = item.querySelector('.services-btn-count__plus')
                let servicePrice = parseInt(item.previousElementSibling.lastElementChild
                    .textContent.replace(/[^\d]/g, ''))
                const oneServicePrice = parseInt(item.previousElementSibling.lastElementChild
                    .textContent.replace(/[^\d]/g, ''))
                let currentServicesNum = parseInt(countServices.textContent)
    
                item.addEventListener('click', (e) => {
                    e.preventDefault()
                    
                    if (e.target == plusServices) {
                        currentServicesNum += 1
                        countServices.textContent = currentServicesNum
                        servicePrice = servicePrice + oneServicePrice
                        item.previousElementSibling.lastElementChild.textContent = formattingPrice(servicePrice)
                    } else if (e.target == minusServices && currentServicesNum > 1) {
                        currentServicesNum -= 1
                        countServices.textContent = currentServicesNum
                        servicePrice = servicePrice - oneServicePrice
                        item.previousElementSibling.lastElementChild.textContent = formattingPrice(servicePrice)
                    }
                    servicePriceTotal = servicePrice
                })
            })
        
            services.addEventListener('click', (e) => {
                let servicePriceNotCount = parseInt(services.parentNode
                    .querySelector('.tab-services__item-price')

                .textContent.replace(/[^\d]/g, ''))
                if (e.target == services) {
                    if (services.checked) {
                        currentTotalPrice = currentTotalPrice + servicePriceNotCount
                    } else {
                        currentTotalPrice = currentTotalPrice - servicePriceNotCount
                    }
                    
                    showPrice(currentTotalPrice)
                } 
            })
        })
        
        if (countDoors) { 
            let currentNumber = parseInt(countDoors.textContent)
            
            minus.addEventListener('click', () => {
                if (currentNumber > 1) {
                    currentNumber -= 1
                    currentTotalPrice = currentTotalPrice - priceOneDoor
                }
                countDoors.textContent = currentNumber
                showPrice(currentTotalPrice)
            })
            
            plus.addEventListener('click', () => {
                currentNumber += 1
                countDoors.textContent = currentNumber
                currentTotalPrice = currentTotalPrice + priceOneDoor
                showPrice(currentTotalPrice)
            })
        }
    }
}

export default calc
